import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController } from 'ionic-angular';

import { Matricula } from '../../models/matricula';
import { MatriculaProvider } from '../../mocks/providers/matricula';

@IonicPage()
@Component({
  selector: 'page-list-master',
  templateUrl: 'list-master.html'
})
export class ListMasterPage {
  currentMatricula: Matricula[];


  constructor(
    public navCtrl: NavController, 
    public matriculaProvider: MatriculaProvider, 
    public modalCtrl: ModalController) {
    this.currentMatricula = this.matriculaProvider.query();
  }

  /**
   * The view loaded, let's query our items for the list
   */
  ionViewWillEnter() {
    this.currentMatricula = this.matriculaProvider.query();
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  /*addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.items.add(item);
      }
    })
    addModal.present();
  } */

  /**
   * Delete an item from the list of items.
   */
  /*deleteItem(item) {
    this.items.delete(item);
  }*/

  /**
   * Navigate to the detail page for this item.
   */
 openMatricula(matricula: Matricula) {
    this.navCtrl.push('ItemDetailPage', {
      matricula: matricula
    });
  }
  verificarConfirmarMatricula(){
    for(let matricula of this.currentMatricula){
      if(matricula.status=='PreMatricula')
      return false;
    }
    return true;
  }
  confirmarMatricula(){
    this.matriculaProvider.confirmarMatricula();
  }
}



